import { getCustomizers, getcustomDesignerselct } from '@/http/api.js';
import tuiLoadmore from '@/plugins/thorui/components/loadmore/loadmore.vue';
import tuiNomore from '@/plugins/thorui/components/nomore/nomore';
export default {
	components: {
		tuiLoadmore,
		tuiNomore
	},
	data() {
		return {
			list: [],
			//参数数组
			sex: [],
			goodat_type: [],
			//参数选择
			checkSort: true,
			sorts: 'rating desc',
			goodat_typeSelected: [],
			sexSelected: '',
			lowPrice: '',
			highPrice: '',
			isSelected: -1, //标签切换
			isActive: true,
			cityName: '区域',
			cityCode: '',
			page: 1,
			loadding: false,
			pullUpOn: true
		};
	},
	onShow() {
		wx.hideHomeButton();
		let pages = getCurrentPages();
		let currPage = pages[pages.length - 1]; //当前页面
		this.cityName = currPage.data.cityName;
		this.cityCode = currPage.data.cityCode;
		if (this.cityName !== '区域') {
			this.list = [];
			this.page = 1;
			this.pullUpOn = true;
			this.getList();
		}
	},
	onLoad(options) {
		this.getConfig();
		this.getList();
	},
	methods: {
		getList() {
			getCustomizers(this.page, this.cityCode, this.sexSelected, this.goodat_typeSelected.toString(), this.sorts).then(res => {
				if (!this.pullUpOn) return;
				this.loadding = true;
				if (res.data.data.length == 0) {
					this.loadding = false;
					this.pullUpOn = false;
					return;
				}
				if (res.data.current_page == res.data.last_page) {
					this.loadding = false;
					this.pullUpOn = false;
					this.list = this.list.concat(res.data.data);
					this.page = this.page + 1;
					return;
				}
				this.list = this.list.concat(res.data.data);
				this.page = this.page + 1;
			});
		},
		//筛选条件接口
		getConfig() {
			getcustomDesignerselct().then(res => {
				res.data.forEach((item, index) => {
					if (item.key == 'sex') {
						this.sex = item.value;
					}
					if (item.key == 'goodat-type') {
						this.goodat_type = item.value;
					}
				});
			});
		},
		//性别参数选择
		checkSex(item) {
			this.sexSelected = item;
		},
		//擅长带团参数选择
		checkGoodat_type(item) {
			if (this.goodat_typeSelected.indexOf(item) == -1) {
				this.goodat_typeSelected.push(item);
			} else {
				this.goodat_typeSelected.splice(this.goodat_typeSelected.findIndex(i => i === item), 1);
			}
		},
		checkSorts(checkSort) {
			this.checkSort = !checkSort;
			if (checkSort) {
				this.sorts = 'rating';
			} else {
				this.sorts = 'rating desc';
			}
			this.list = [];
			this.page = 1;
			this.pullUpOn = true;
			this.getList();
		},
		navSelect(index) {
			if (index == 3) {
				uni.navigateTo({
					url: '/pages/selectCity/selectCity'
				});
			}
			if (this.isSelected === index) {
				this.isSelected = -1;
			} else {
				this.isSelected = index;
			}
		},
		lowP(e) {
			this.lowPrice = e.detail.value;
		},
		highP(e) {
			this.highPrice = e.detail.value;
		},
		//重置
		reset() {
			this.list = [];
			// this.goodat_type = [];
			this.goodat_typeSelected = [];
			this.page = 1;
			this.pullUpOn = true;
			this.languageSelected = [];
			this.yearsSelected = '';
			this.sexSelected = '';
			this.ageSelected = '';
			this.lowPrice = '';
			this.highPrice = '';
			this.isSelected = -1;
			this.getList();
		},
		//完成
		complete() {
			this.list = [];
			this.page = 1;
			this.pullUpOn = true;
			this.getList();
			this.languageSelected = [];
			this.yearsSelected = '';
			// this.sexSelected = '';
			this.ageSelected = '';
			this.isSelected = -1;
			this.lowPrice = '';
			this.highPrice = '';
		},
		toDetail(id) {
			uni.navigateTo({
				url: '/pages/studio/studio?id=' + id
			});
			uni.setStorageSync('studio', id);
		}
	},
	// 页面上拉触底事件的处理函数
	onReachBottom: function() {
		this.getList();
	}
};
